const initialWords = ["one", "two"];

const initialState = {
  title: "Title 1",
  words: initialWords
};

export const app = {
  state: initialState,

  // DON'T use `app` variable in methods
  // Instead use this

  showWords() {
    return this.state.words.join("-");
  },
  addWord() {
    return this.state.words.push();
  }

  // add more methods here
};
